.movie-presentation__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 27px;
  margin-top: 0;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 26px;
    margin-bottom: 32px;
  }

  @media (min-width: $desktop-width) {
    font-size: 30px;
    line-height: 26px;
    margin-top: 0;
  }
}

.movie-presentation__intro {
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
  margin-top: 0;
  margin-bottom: 38px;
  padding-left: 8px;
  padding-right: 6px;

  br {
    display: none;
  }

  @media (min-width: $tablet-width) {
    font: inherit;
    margin-bottom: 89px;
    padding-left: 100px;
    padding-right: 100px;

    br {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    font: inherit;
    padding-left: 125px;
    padding-right: 125px;

    br {
      display: block;
    }
  }
}

.movie-presentation__image {
  width: 100%;
  display: block;
  background-color: rgb(184, 166, 172);

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}

.movie-presentation {
  @media (min-width: $tablet-width) {
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 42px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 199px;
    padding-right: 201px;
    padding-bottom: 82px;
  }
}

.movie-presentation__play {
  background-image: url("../img/mobile/play.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 48px;
  height: 54px;
  border: none;
  background-color: transparent;
  position: absolute;
  left: 53%;
  bottom: 34%;
  transform: translateX(-50%);

  &:focus-visible {
    outline: none;
    box-shadow: 0 0 0 2px #669ec0;
  }

  @media (min-width: $tablet-width) {
    left: 50%;
    bottom: 44%;
  }
}

.movie-presentation__wrapper {
  position: relative;
}
